/* sass variables */
.range-label {
  color: #d1d1d1;
  font-size: 0.625rem;
  text-transform: uppercase;
  margin-bottom: 0.625rem;
  display: block; }

.range-input {
  color: #d1d1d1;
  font-size: 0.625rem;
  text-align: center;
  width: 2rem;
  height: 1.25rem;
  background: #2b2b2b;
  border: none;
  border-radius: 0.25rem; }

.range-wrapper {
  display: flex; }

.range-input {
  margin-left: 1.25rem; }

.range-slider {
  width: 100%;
  height: 0.875rem;
  border-radius: 1.875rem;
  background-color: #2b2b2b;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none; }
  .range-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    cursor: pointer;
    height: 1.125rem;
    width: 1.125rem;
    background-color: #fff;
    border: 0.25rem solid #fff;
    border-radius: 50%;
    background: #2b2b2b; }
    .range-slider::-webkit-slider-thumb:active, .range-slider::-webkit-slider-thumb:hover, .range-slider::-webkit-slider-thumb:focus {
      border-color: #00e676; }
  .range-slider::-moz-range-thumb {
    box-sizing: border-box;
    cursor: pointer;
    height: 1.125rem;
    width: 1.125rem;
    background-color: #fff;
    border: 0.25rem solid #fff;
    border-radius: 50%;
    background: #2b2b2b; }
    .range-slider::-moz-range-thumb:hover, .range-slider::-moz-range-thumb:active, .range-slider::-moz-range-thumb:focus {
      border-color: #00e676; }
