/* sass variables */
.label {
  color: #d1d1d1;
  font-size: 0.625rem;
  text-transform: uppercase;
  margin-bottom: 0.625rem;
  display: block; }

.input {
  opacity: 0;
  width: 0;
  height: 0;
  position: absolute;
  left: 0;
  top: 0; }

.option {
  position: relative;
  cursor: pointer;
  flex-grow: 1;
  text-align: center;
  padding: 0.375rem;
  border: 4px solid transparent; }

.option-active {
  composes: option;
  border-color: #00e676; }

.options {
  display: flex;
  max-width: 11.25rem;
  border-radius: 0.25rem;
  background-color: #2b2b2b;
  overflow: hidden; }
