/* sass variables */
.control-panel {
  padding: 2rem;
  min-width: 18.75rem;
  max-width: 21.875rem;
  background-color: #111; }

.controls-set {
  display: flex;
  margin-top: 1.5rem; }

.item {
  flex-grow: 1; }

.heading {
  font-size: 0.875rem;
  font-weight: 700;
  color: #fff;
  margin: 3rem 0 1.5rem; }
  .heading:first-child {
    margin-top: 0; }

.result {
  font-size: 0.75rem;
  margin-top: 2.5rem; }
