/* sass variables */
.point {
  stroke: #555;
  stroke-width: 5;
  fill: #fff;
  cursor: pointer;
  transition: fill 0.2s; }
  .point:hover {
    fill: #00e676; }

.anchor {
  stroke: #888;
  stroke-width: 5;
  fill: #fff;
  cursor: pointer;
  opacity: 0.6; }

.anchor-active {
  stroke: #888;
  opacity: 1;
  fill: #fff; }

.point-active {
  fill: #00e676; }

.point-first {
  stroke: #00e676; }

.line {
  stroke: #888;
  stroke-dasharray: 5 5;
  stroke-width: 1;
  opacity: 0.6; }

.line-active {
  opacity: 1; }
