/* sass variables */
.label {
  color: #d1d1d1;
  font-size: 0.625rem;
  text-transform: uppercase;
  margin-bottom: 0.625rem;
  display: block; }

.switch {
  position: relative;
  width: 2rem;
  height: 0.875rem; }

.input {
  opacity: 0;
  width: 0;
  height: 0; }
  .input:checked + .slider:before {
    left: auto;
    right: 0;
    border-color: #00e676; }

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #2b2b2b;
  border-radius: 1.875rem; }
  .slider:before {
    position: absolute;
    display: block;
    content: "";
    cursor: pointer;
    height: 1.125rem;
    width: 1.125rem;
    left: 0;
    top: -0.125rem;
    background-color: #fff;
    border: 0.25rem solid #fff;
    border-radius: 50%;
    background: #2b2b2b; }
