/* sass variables */
.svg-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
  background-color: #f3f3f3;
  position: relative; }

.svg {
  background-color: #fff;
  flex-shrink: 0;
  margin: 1.25rem; }

.path {
  stroke: #555;
  stroke-width: 4px;
  fill: none; }

.instruction {
  align-self: flex-end;
  width: 100%;
  background-color: #fff;
  border-top: 3px solid #ebebeb;
  padding: 1.25rem;
  color: #2b2b2b;
  text-transform: uppercase;
  font-size: 0.6875rem; }
  .instruction p {
    padding-bottom: 0.5rem; }
  .instruction b {
    border-bottom: 2px solid #00e676; }
